@import url(https://fonts.googleapis.com/css2?family=Volkhov&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Bree+Serif&display=swap);
.userPage {
  display: flex;
}
.jobFormWrapper {
}
.jobForm-grid-wrapper {
  border: 2px, solid, black;
  margin-top: 2rem;
  margin-left: 2rem;
  display: grid;
  background-color: rgba(9, 11, 14, 0.7);
  color: #fffded;
  grid-gap: 5px;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 10%;
}

.jobForm-grid-wrapper > * {
  padding: 1rem;
}

.jobForm-grid-wrapper button {
  background-color: #fffded;
}

.jobItem {
  padding: 1rem;
}

.CreateJobForm Button {
  margin-top: 1rem;
}

.textField {
  width: 20%;
}

.sideBar {
  height: 100vh;
  min-width: 12rem;
  background-color: rgba(9, 11, 14, 0.7);
}

.sideBarHeader {
  color: white;
  line-height: 4rem;
  text-align: center;
  background-color: rgba(27, 32, 39, 0.315);
}

.sideBar li {
  display: flex;
  flex-direction: row;
  line-height: 4rem;
  justify-content: center;
  height: 100%;
  width: 100%;
  font-weight: bold;
  font-size: 16px;
  color: white;
  list-style-type: none;
  cursor: pointer;
  box-sizing: border-box;
  border-top: solid rgb(80, 80, 104, 1) 1px;
  border-bottom: solid black 1px;
  transition: 0.3s;
}

.icon {
  margin-left: 5px;
  flex: 30% 1;
}

.listTitle {
  flex: 70% 1;
}

.sideBar li:hover {
  margin-left: 20px;
  background-color: rgba(58, 60, 66, 1);
}

.table {
  flex: 1 1;
  display: flex;
  justify-content: center;
  float: right;
  padding: 20px;
  margin-left: 0rem;
}

.statTable {
  width: 98%;
  margin-top: 8rem;
  border-collapse: collapse;
}

.statTable th {
  padding: 6px 10px;
  border: 1px solid black;
}

.statTable td {
  font-size: 0.9rem;
  padding: 6px 10px;
  border: 1px solid black;
}

.tableHead {
  height: 4rem;
  background-color: rgba(9, 11, 14, 0.7);
  font-size: 1rem;
  text-align: center;
  font-weight: bold;
  color: white;
  padding-top: 2rem;
}

.tableHead th {
  padding-left: 1rem;
  padding-right: 1rem;
}

.tdStyle tr {
  background-color: white;
}

.statTable tbody tr:nth-child(odd) > td {
  background-color: white;
}

.footStyleDark {
  background-color: rgba(9, 11, 14, 0.7);
  color: white;
  text-align: left;
}

.footStyleWhite {
  background-color: white;
  color: white;
  text-align: right;
}
/* ---------------------------------------- */
.welcome {
  font-size: 20px;
}

.inTheGreen {
  font-size: 15px;
  background-color: rgba(144, 238, 144, 0.322);
}

.inTheRed {
  background-color: rgba(255, 0, 0, 0.342);
}

.font1 {
  font-weight: bold;
  font-size: 1.1rem;
}

.pageWrapper {
  background-image: url(/static/media/landingPageBackground.4ca40105.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  background-attachment: fixed;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.menuWrapper {
  background-color: rgba(200, 205, 208, 1);
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

* {
  font-family: volkhov;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

/* NAV STYLES */

.appNav {
  margin-top: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.2% 10%;
  background-color: rgba(9, 11, 14, 0.7);
  border: solid black 1px;
}

.appNav p {
  font-size: 30px;
  color: white;
  margin-left: 30rem;
}

.nav {
  margin-top: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.2% 10%;
  background-color: rgba(72, 79, 86, 0.7);
}
.nav-logo {
  padding-top: 4px;
  border-radius: 1rem;
  height: auto;
  width: auto;
  max-width: 7.5rem;
  max-height: 12rem;
}

.nav-link {
  font-weight: 500;
  font-size: 15px;
  text-decoration: none;
  color: #fffded;
  display: inline-block;
  padding: 1rem 1rem 1rem 1rem;
}

/* CONTENT STYLES */

.content1 {
  width: 50%;
  border: solid, black, 2px;
  border-radius: 20px;
  margin-top: 10rem;
  height: auto;
  margin-left: 25rem;
  margin-right: 8rem;
  padding-top: 3rem;
  padding-bottom: 3rem;
}

.headtext1 {
  text-align: center;
  font-size: 40px;
  font-weight: 400;
  color: #fffded;
}

.bodytext1 {
  text-align: center;
  margin-top: 2%;
  font-size: 22px;
  color: #fffded;
}

.loginWrapper {
  min-width: 50%;
  height: 50%;
}

.loginHead {
  padding: 10px;
}

.formText {
  font-size: 20px;
  font-weight: bold;
}

.form {
  color: #fffded;
  background-color: rgba(72, 79, 86, 0.9);
  border: solid, black, 1px;
  border-radius: 20px;
  margin-top: 8rem;
  width: 20rem;
  height: 20rem;
}

.form Button {
  width: 50%;
}

.form form {
  margin-top: 2rem;
}

.form Button {
  margin-top: 1rem;
}

.selectionButton {
  margin-top: 3rem;
}
.buttonFormButton {
  width: 60%;
}

.registrationWrapper {
  padding-top: 2rem;
  margin: auto;
}
.editWrapper {
}

.subRegistrationForm {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-auto-rows: minmax(10%, auto);
  border: solid, black, 1px;
  border-radius: 20px;
  width: 60%;
  background-color: rgba(72, 79, 86, 0.9);
}

.subRegistrationForm > * {
  padding: 1rem;
}

.subRegistrationForm Button {
  margin-top: 3rem;
}
.subItem00 {
  grid-column: 1/-1;
}
.subItem0 {
  color: #fffded;
  grid-column: 1/-1;
}

.subItem12 {
}

.vendorRegistrationForm {
  display: grid;
  grid-gap: 5px;
  grid-template-columns: 1fr 1fr;
  grid-auto-rows: minmax(10%, auto);
  border: solid, black, 1px;
  border-radius: 20px;
  width: 40rem;
  background-color: rgba(72, 79, 86, 0.9);
}

.vendorRegistrationForm > * {
  padding: 1rem;
}

.vendorItem0 {
  color: #fffded;
  grid-column: 1/-1;
}

.vendorItem5 {
  grid-column: 1/-1;
}

.vendorItem8 {
  grid-column: 1/-1;
}

.vendorItem9 {
  grid-column: 1/-1;
  padding-top: 3rem;
}

.editSubcontractorForm {
  display: grid;
  margin-top: 2rem;
  margin-left: 2rem;
  grid-template-columns: 1fr 1fr;
  grid-auto-rows: minmax(10%, auto);
  border: solid, black, 1px;

  width: 60%;
  background-color: rgba(9, 11, 14, 0.7);
}

.editSubcontractorForm > * {
  padding: 1rem;
}

.editSubcontractorForm Button {
  margin-top: 3rem;
}
.subItem00 {
  grid-column: 1/-1;
}
.subItem0 {
  color: #fffded;
  grid-column: 1/-1;
}
.subItemEdit5 {
  grid-column: 1/-1;
}

.subItem12 {
}

.VendorEditForm {
  display: grid;
  margin-top: 2rem;
  margin-left: 2rem;
  grid-gap: 5px;
  grid-template-columns: 1fr 1fr;
  grid-auto-rows: minmax(10%, auto);
  border: solid, black, 1px;
  width: 40rem;
  background-color: rgba(9, 11, 14, 0.7);
}

.VendorEditForm > * {
  padding: 1rem;
}

.vendorItem0 {
  color: #fffded;
  grid-column: 1/-1;
}

.vendorItem5 {
  grid-column: 1/-1;
}

.vendorItem8 {
  grid-column: 1/-1;
}

.vendorItem9 {
  grid-column: 1/-1;
  padding-top: 3rem;
}
/* FOOTER STYLES */

.footcontent {
  margin-top: auto;
  margin-bottom: 3rem;
}

.footer li {
  display: inline-block;
  padding: 1rem 1rem 1rem 1rem;
}

.footNavBar {
  text-align: center;
  padding-left: 10rem;
  position: absolute;
  width: 100%;
  background-color: rgba(72, 79, 86, 0.8);
  color: #fffded;
}

.footer img {
  float: right;
  padding-top: 0.7rem;
  padding-left: 1rem;
  padding-right: 1rem;
}

